.card {
	justify-content: space-between;
	min-height: 550px;
	margin-bottom: 2rem;
	padding: 30px;
	max-width: 365px;
}

.card-title {
	margin-top: 1rem;

	font-size: 1.25rem;
	font-weight: bold;
	text-align: center;
	color: #3b76c4;
}

.card-desc {
	text-align: justify;
	text-indent: 1rem;
	color: #514547;
}

.card-image {
	height: 200px;
	border-radius: 10px;
	box-shadow: 3px 2px 5px 0 #b7cce9;
}

.card .btn {
	background-color: darkred;
	color: white;
	transition: all 0.5s;
}

.card .btn:hover {
	background-color: red;
}

@media(min-width: 1200px) {
	.card {
		max-width: 370px;
	}
}

@media(max-width: 1199px) {
	.card {
		max-width: 300px;
	}
}

@media(max-width: 991px) {
	.card {
		max-width: 345px;
	}
}

@media(max-width: 767px) {
	.card {
		max-width: 100%;
	}

	.card-image {
		height: auto;
	}
}